import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { PictureCardBankComponent } from './picture-card-bank/picture-card-bank.component';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { DisplayPictureComponent } from './display-picture/display-picture.component';
import { IndexPageComponent } from './index-page/index-page.component';
import { ChangePictureComponent } from './change-picture/change-picture.component';
import { MyPicturesComponent } from './my-pictures/my-pictures.component';
import { ExitComponent } from './exit/exit.component';

const routes: Routes = [
  {
    path: 'pictureCard',
    component: PictureCardBankComponent
  },
  {
    path: 'displayPicture',
    component: DisplayPictureComponent
  },
  {
    path: '',
    component: IndexPageComponent
  },
  {
    path: 'changePicture',
    component: ChangePictureComponent
  },
  {
    path: 'myPictures',
    component: MyPicturesComponent
  },
  {
    path: 'exit',
    component: ExitComponent
  }
];

@NgModule({
  declarations: [
    AppComponent,
    PictureCardBankComponent,
    DisplayPictureComponent,
    IndexPageComponent,
    ChangePictureComponent,
    MyPicturesComponent,
    ExitComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    ReactiveFormsModule,
    FormsModule,
    RouterModule.forRoot(routes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})

export class AppModule { }
